declare var swal: any;
import { Component, OnInit } from '@angular/core';
import { errorMessage } from 'src/app/messages';
import { DataService } from 'src/app/service/data.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { BrandService } from 'src/app/service/brand.service';
import { ISubscription } from "rxjs/Subscription";
import { DropDownService } from 'src/app/service/drop-down.service';

@Component({
  selector: 'app-brand-list',
  templateUrl: './brand-list.component.html'
})
export class BrandListComponent implements OnInit {

  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  category_id:string=null;
  private subscription: ISubscription;
  categoryList: any=[];
  constructor(
    private BrandService: BrandService,
    private dataService: DataService,
    private dropdownService:DropDownService,
    private paginationService: PaginationService
  ) {  }

  ngOnInit() {
    this.subscription= this.dataService.searchTextSubject.subscribe((v) =>{ 
      this.searchText=v;
      this.getObjects();
      this.getCategoryList();
    });
  }

  getCategoryList() {
    this.dropdownService.getCategoryDrp().subscribe((res) => {
      if (res.success) {
        this.categoryList = res.data ? res.data : [];
      }
    });
  }

  getObjects() {
    this.loadingState=true;
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    if(this.category_id){
      params.category_id=this.category_id;
    }
    this.BrandService.getBrandList(params).subscribe((response) => {
      // console.log(response);
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.data;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
      // console.log(error);
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.getObjects();
  }

  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.BrandService.deleteBrand(object.id).subscribe(
          (response) => {
            if (response.success) {
              this.getObjects();
            }
          },
        );
      }
    }).catch(swal.noop);
  }

  getjson(data){
    if(data)
      return JSON.parse(data);
  }
  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
  
}
