import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { BrandListComponent } from './brand-list.component';
import { BrandService } from 'src/app/service/brand.service';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { FormsModule } from '@angular/forms';
import { NgSelectModule } from '@ng-select/ng-select';


const routes: Routes = [
  {
    path: '',
    component: BrandListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../brand-add/brand-add.module').then(m => m.BrandAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../brand-add/brand-add.module').then(m => m.BrandAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    BrandListComponent
  ],
  imports: [
    CommonModule,
    PaginationModule,
    LoderModule,
    FormsModule,
    NgSelectModule,
    RouterModule.forChild(routes)
  ],
  providers: [
    BrandService
  ]
})
export class BrandListModule { }
